:root {
  --canopy-green: #014038;
  --meadow-mist: #96BAA0;
  --sky-bloom: #BDD4DA;
  --sunlit-petal: #F0C06D;
  --cloudy-grey: #F7F7F7;
  --willow-gray: #C5C3C3;
}

:root {
  --font-calibri: "Calibri", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(Com);
  background: var(--canopy-green);
  color: var(--willow-gray);
}

/* Example:
h1 {
  font-size: rem(30);  <-- this is 1.875rem
}
*/
h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
}
@media (min-width: 59.375em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 59.375em) {
  .container {
    padding: 0;
  }
}

.header {
  background-color: #BDD4DA;
  color: #014038;
  padding: 15px 0;
  text-align: center;
}
@media (min-width: 59.375em) {
  .header {
    display: flex;
    justify-content: space-between;
  }
}
.card {
  padding: 1.25rem;
}